/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Arial", sans-serif;
  background-color: #f4f4f4;
  color: #333;
}

.App {
  text-align: center;
}

header {
  background-color: #282c34;
  padding: 50px 0;
  color: white;
}

h1 {
  font-size: 3rem;
}

section {
  padding: 50px 0;
}

/* Navbar Styles */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #333;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  z-index: 1000;
  transition: all 0.5s ease-in-out;
}

.navbar.hidden {
  transform: translateY(-100%);
}

.navbar.visible {
  transform: translateY(0);
}

.navbar-logo {
  color: white;
  font-size: 1.5rem;
}

.navbar-links {
  display: flex;
}

.navbar-links a {
  color: white;
  text-decoration: none;
  margin-left: 20px;
  transition: color 0.3s;
}

.navbar-links a:hover {
  color: #61dafb;
}

/* Burger Menu Styles */
.burger-menu {
  display: none;
  cursor: pointer;
}

.burger-icon {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 25px;
  height: 20px;
}

.line {
  background-color: white;
  height: 3px;
  width: 100%;
}

/* Mobile Menu Styles */
.mobile-menu {
  position: fixed;
  top: 0;
  right: 0;
  background-color: #333;
  width: 200px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding-top: 50px;
  z-index: 1000;
}

.mobile-menu a {
  color: white;
  text-decoration: none;
  margin: 20px;
  font-size: 1.5rem;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .navbar-links {
    display: none;
  }

  .burger-menu {
    display: block;
  }
}
